(function() {
  'use strict';

  angular
    .module('moto', [
      'ngAnimate',
      'ngCookies',
      'ngTouch',
      'ngSanitize',
      'ngMessages',
      'ngAria',
      'ngResource',
      'ngRoute'
    ])

    .filter('encodeURI', encodeURI)                 // Filter for URL
    .filter('startsWithLetter', startsWithLetter)   // Filter for search input
    .filter('property', property)                   // Filter for category
    .filter('limitString', limitString);            // Filter for character limit

    function encodeURI() {
        return encodeURI;
    }

    function startsWithLetter() {

      return function (items, letter) {
        var filtered = [];
        var l = items.length;
        var letterMatch = new RegExp(letter, 'i');
        for (var i = 0; i < l; i++) {
          var item = items[i];
          if (letterMatch.test(item.title.substring(0, 32))) {
            filtered.push(item);
          }
        }
        return filtered;
      };

    }

    function property($filter) {

      var parseString = function(input){
        return input.split('.');
      };

      function getValue(element, propertyArray) {
        var value = element;

        angular.forEach(propertyArray, function(property) {
          value = value[property];
        });

        return value;
      }

      return function (array, propertyString, target) {
        var properties = parseString(propertyString);

        return $filter('filter')(array, function(item){
          return getValue(item, properties) === target;
        });
      };

    }

    function limitString() {

      return function (value, wordwise, max, tail) {
        if (!value) return '';

        max = parseInt(max, 10);
        if (!max) return value;
        if (value.length <= max) return value;

        value = value.substr(0, max);
        if (wordwise) {
          var lastspace = value.lastIndexOf(' ');
          if (lastspace != -1) {
            value = value.substr(0, lastspace);
          }
        }

        return value + (tail || ' â€¦');
        // usage ex:  | limitString:true:100:' ...'
      };
    }

})();

(function () {
  'use strict';

  angular
    .module('moto')
    .directive('motoNavbar', motoNavbar);

  /** @ngInject */

    function motoNavbar() {

      return {
        restrict: 'E',
        templateUrl: 'app/components/navbar/navbar.html',
        controller: 'NavbarController',
        controllerAs: 'vm',
        link: function($scope) {

          // TODO: use $document to get elements
          $scope.wrapper = document.getElementById('site-wrapper');
          $scope.navIcon = document.getElementById('cross');
          $scope.navTag = document.getElementsByClassName('linkName');

          function navFn() {
            if ($scope.wrapper.classList.contains('show-nav') === false) {
              $scope.wrapper.classList.add('show-nav');
              $scope.navIcon.classList.add('cross');
            } else {
              $scope.wrapper.classList.remove('show-nav');
              $scope.navIcon.classList.remove('cross');
            }
          }

          // bind on click function to Menu
          $scope.navIcon.addEventListener('click', navFn, false);

          // bind on click function to Menu List Items
          var navTagFunction = function() {

            var subMenu = this.nextElementSibling;

            if(subMenu !== null && subMenu.classList.contains('expandItem') === false) {
              subMenu.classList.add('expandItem');
            } else {
              subMenu.classList.remove('expandItem');
            }

          };

          for(var i=0; i<$scope.navTag.length; i++) {
            $scope.navTag[i].addEventListener('click', navTagFunction, false);
          }

          // TODO: Add hammer swipe function
          //// create a hammer swipe instance
          //$scope.vm = new Hammer($scope.wrapper);
          //
          //// listen to swipe events
          //$scope.vm.on('swiperight', function(e) {
          //  $scope.wrapper.classList.add('show-nav');
          //  $scope.navIcon.classList.add('cross');
          //  e.preventDefault();
          //});
          //$scope.vm.on('swipeleft', function(e) {
          //  $scope.wrapper.classList.remove('show-nav');
          //  $scope.navIcon.classList.remove('cross');
          //  e.preventDefault();
          //});

        }

      };

  }

}());

(function () {
 'use strict';

  angular
    .module('moto')
    .controller('NavbarController', NavbarController);

    /** @ngInject */

    function NavbarController($location) {

      var vm = this;

      vm.menuClass = function(page) {

        vm.current = $location.path().substring(1);
        return page === vm.current ? 'ms-menu__active' : '';

      };

    }

}());

(function() {
  'use strict';

  /** @ngInject */

angular
  .module('moto')
  .directive('carouselar', carouselar);

  function carouselar($window, CarouselarConstants) {
        return {
          replace: true,
          restrict: 'AE',
          scope: {
            images: '='
          },
          templateUrl: 'app/components/carousel/carousel.html',
          controller: 'CarouselarController',
          controllerAs: 'vm',
          link: function(scope, element, attrs) {

            scope.vm.maxImageCount = Math.min(
            parseInt(attrs.displayingImageCount), CarouselarConstants.MAX_DISPLAYING_IMAGE) || 1;

            var win = angular.element($window);
            win.bind('resize', scope.onResize);
            scope.onResize();

            scope.$on('$destroy', function() {
              win.unbind('resize', scope.onResize);
            });

          }
        };
      }

angular
  .module('moto')
  .directive('carouselarImage', carouselarImage);

  function carouselarImage($compile) {
    return {
      restrict: 'E',
      scope: {
        isVisible: '=',
        imgUrl: '@',
        date: '@',
        categoryUrl: '@',
        tag: '@',
        headingUrl: '@'
      },
      template: '<div class="carouselar__loader" ng-if="vm.isLoading"></div>',
      controller: 'CarouselarImageController',
      controllerAs: 'vm',
      link: function(scope, element) {

        scope.vm.compileImage = function() {

          element.append(
              '<img class="carouselar__image" carouselar-loader on-load="vm.onLoad()" ng-src="{{imgUrl}}"/>');

          element.append(
              '<date class="carouselar__date">{{date}}</date>');

          element.append(
              '<h4 class="carouselar__heading"><a ng-href="news/{{categoryUrl}}/{{headingUrl}}">{{headingUrl}}</a></h4>');

          element.append(
              '<span class="carouselar__cat"><a ng-href="news/{{tag}}">{{tag}}</a></span>');

          $compile(element.contents())(scope);

        };
      }
    };
  }

angular
  .module('moto')
  .directive('carouselarLoader', carouselarLoader);

  function carouselarLoader() {
    return {
      restrict: 'A',
      scope: {
        onLoad: '&'
      },
      link: function(scope, element) {
        element.bind('load', scope.onLoad);
      }
    };
  }

})();

(function() {
  'use strict';

  /** @ngInject */

angular
  .module('moto')
  .controller('CarouselarController', CarouselarController);

  function CarouselarController($scope, $timeout, $window, CarouselarConstants, news, $interval) {

    var vm = this;

    news.list(function(news) {
      vm.news = news;
    });

    vm.visibleImages = [];
    vm.maxImageCount = 1;
    // TODO: Fix controller as to 'vm'
    $scope.displayingImageCount = 0;
    vm.activeSection = 0;
    vm.sectionCount = 5;
    vm.containerPosition = 'translateX(0)';
    vm.singleImageWidth = '100%';

    vm.createArray = function(num) {
      return new Array(num || 0);
    };

    vm.prev = function() {
      vm.moveToSection(vm.activeSection - 1);
    };

    vm.next = function() {
      vm.moveToSection(vm.activeSection + 1);
    };

    $interval( function(){
      vm.next();
    },5000);

    vm.moveToSection = function(sectionIndex) {
      if (sectionIndex >= vm.sectionCount) {
        vm.activeSection = 0;

      } else if (sectionIndex < 0) {
        vm.activeSection = vm.sectionCount - 1;

      } else {
        vm.activeSection = sectionIndex;
      }

      // update container position & visible images:
      var pos;
      var imagePercentage = 100 / $scope.displayingImageCount;
      var visibleImages = [];
      var i;

      if (vm.activeSection === vm.sectionCount - 1) { // last section
        pos = (vm.news.length - $scope.displayingImageCount) * imagePercentage;

        for (i = 0; i < $scope.displayingImageCount + 1; i++) {
          visibleImages.push(vm.news.length - i);
        }

      } else {
        var firstVisibleImageIndex = vm.activeSection * $scope.displayingImageCount;

        visibleImages.push(firstVisibleImageIndex);
        for (i = 1; i < $scope.displayingImageCount; i++) {
          visibleImages.push(firstVisibleImageIndex + i);
        }

        pos = firstVisibleImageIndex * imagePercentage;
      }

      vm.containerPosition = 'translateX(-' + pos + '%)';
      vm.visibleImages = visibleImages;
    };

    vm.resizeTimer = null;
    $scope.onResize = function(event, _width) {
      $timeout.cancel(vm.resizeTimer);
      vm.resizeTimer = $timeout(function() {
        var displayingImageCount = vm.maxImageCount;
        var windowWidth = _width || $window.innerWidth;

        if (windowWidth < CarouselarConstants.BREAKPOINTS.LANDSCAPE) {
          displayingImageCount = Math.ceil(vm.maxImageCount / 2);

          if (windowWidth < CarouselarConstants.BREAKPOINTS.PORTRAIT) {
            displayingImageCount = 1;
          }
        }

        $scope.displayingImageCount = displayingImageCount;
      }, CarouselarConstants.RESIZE_TIMEOUT);
    };

    $scope.$watch('displayingImageCount', displayingImageCount);

    function displayingImageCount(newValue) {
      if (newValue) {
        vm.sectionCount = Math.ceil(
        vm.news.length / $scope.displayingImageCount);
        vm.moveToSection(vm.activeSection); // TODO: keep first image in sight
        vm.singleImageWidth = (100 / newValue) + '%';
      }
    }

    vm.isImageVisible = function(imageIndex) {
      return vm.visibleImages.indexOf(imageIndex) > -1;
    };

  }

angular
  .module('moto')
  .controller('CarouselarImageController', CarouselarImageController);

  function CarouselarImageController($scope) {
    var vm = this;
    vm.isLoading = true;
    vm.onLoad = function() {
      vm.isLoading = false;
      $scope.$apply();
    };

    var unwatch = $scope.$watch('isVisible', isVisible);
    function isVisible(isVisible) {
      if (isVisible) {
        vm.compileImage();
        unwatch();
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('moto')
    .factory('news', news);

      /** @ngInject */

      function news($http) {

        function getData(callback) {
            $http({
              method: 'GET',
              url: 'app/data/articles.json',
              cache: true
            }).success(callback);
        }

        return {
          list: getData,
          find: function (title, callback) {
              getData(function (data) {
                  var news = data.filter(function (entry) {
                      return entry.title === title;
                  })[0];
                  callback(news);
              });
          }
        };

      }

})();

